.collection-list-media {
  &__box {
    @apply tw-relative tw-overflow-hidden;

    &:before,
    &:after {
      content: "";
      @apply tw-absolute;
    }

    &:before {
      @apply tw-top-0 tw-left-0 tw-bg-white tw-w-2/5 tw-h-full;
    }
    &:after {
      border-bottom: 20rem solid transparent;
      border-left: 6rem solid;
      @apply tw-bottom-0 tw-left-[40%] tw-z-0 tw-border-l-white;
    }
  }

  &__content {
    @apply tw-bottom-0 tw-flex tw-flex-col tw-h-full tw-justify-between tw-left-0 tw-absolute tw-p-4 tw-w-full tw-z-10;
  }

  &__image {
    @apply tw-w-full tw-h-full tw-block tw-object-cover;
  }

  &__logo {
    @apply tw-w-fit tw-h-8;
  }

  &__action {
    @apply tw-text-primary tw-text-xs tw-uppercase tw-no-underline;
  }
}
